import { useEffect, useState } from "react";
import { useLocation} from "react-router-dom";
import { getCategory, getProduct } from "../components/ApiFunction/function";
import Product from "../components/Product/Product";
import ErrorBoundary from "../components/Errors/Error";
import ErrorMsg from "../components/Errors/ErrorMsg";
import Filter from "../components/FilterProducts/Filter";
import { useProduct } from "../components/Contexts/ProductContext";
import { useAuth } from "../components/Contexts/AuthContext";
import ProdCreate from "../components/Create/CreateProd";
import Search from "../components/SearchProducts/Search";
const Products = () => {
    
    const location = useLocation();
    const categProd = location.pathname.split('/product/').join('');

    const { 
        product, 
        setProduct, 
        categ, 
        setCateg,
        active,
        setActive,
    } = useProduct();

    const { status } = useAuth();

    useEffect(()=> {
        if(active) {
            getProduct(categProd, setProduct);
            getCategory(categProd, setCateg);
            setActive(false);
        } 
    }, [active, categProd])

    return(
        <div className="main__container">
            {status === 'admin' ?
            <ProdCreate/>:''
            }
            <Search/> 
            <div className="rigths flex">
                <Filter/>
                <ErrorBoundary ErrorComponent={ErrorMsg}>
                    <div className="products">
                        <h1 className="title">
                            {categ[2] === categProd ? categ[1]: ''}
                        </h1>
                        <div className="center flex">
                            {product && product.length !== 0 ? 
                                <Product nameProd={product} nameCateg={categProd} state={setActive}/>
                                : 'Нет доступных товаров данной категории'
                            }
                        </div>
                    </div>
                </ErrorBoundary>
            </div>
        </div>
    )   
}

export default Products;