import { useEffect } from "react";
import { useProduct } from "../Contexts/ProductContext";

import './search.css';
import { getProduct } from "../ApiFunction/function";
import { useLocation } from "react-router-dom";

const Search = () => {
   const location = useLocation();
   const categProd = location.pathname.split('/product/').join('');
   const { 
      product,
      setProduct,
      searchValue,
      setSearchValue,
   } = useProduct();

   const searchElem = (searchElem) => {
      setSearchValue(searchElem);
      if(searchValue !== '') {
         const filteredElems = product.filter((item) => {
            return Object.values(item).join('').toLowerCase().includes(searchValue.toLowerCase())
         })
         setProduct(filteredElems);
         console.log(filteredElems);
      } else {
         getProduct(categProd, setProduct);
      }
   }

   useEffect(() => {
      if (searchValue === '') {
        getProduct(categProd, setProduct);
      }
    }, [searchValue, categProd, setProduct]);

   return(
      <>
         <div className="main__search flex">
            <input className="main__search--input" 
            type="search" 
            onChange={(e) => searchElem(e.target.value)}
            placeholder="Поиск..."/>
            <div className="main__search--icon" />
         </div>
      </>
   )
}

export default Search;