import { useLocation } from "react-router-dom";
import { getCategory, getProduct, searchItem } from "../ApiFunction/function";
import { generateKey } from "../CheckForm/Checking";
import { useBrand } from "../Contexts/BrandContext";
import { useProduct } from "../Contexts/ProductContext";
import { useEffect, useState } from "react";

const Brand = () => {

   const location = useLocation();
   const prod = location.pathname.split('/product/').join('');

   const {brand, setBrand} = useBrand();
   const { categ, product, setProduct, setActive, setCateg } = useProduct()

   const [cond, setCond] = useState(true),
         [state, setState] = useState(true);

   const handleSubmit = (brand, categ) => {
      if(brand) {
         searchItem(brand, categ, setProduct)
         setActive(true)
      }
   }

   useEffect(()=> {
      if (product && product.length !== 0) {
         let arr = product.map(item => {return item.brand})
         let filtered = arr.filter((curr, ind) => {
            // console.log(curr, ind); 
            return arr.indexOf(curr) === ind})
         setBrand(filtered);
         setState(false)
      }
   }, [state, product])

   if(cond) {
      getProduct(prod, setProduct);
      getCategory(prod, setCateg);
      setCond(false);
   }

   return (
      <div className="right__brands">
         <h3 className="filter__title">Бренды</h3>
         <div className="brand flex">
            {brand && brand.length !==0 ? 
            brand.map(item => {
               return(
                     <div key={generateKey(item.id)} >
                        <button
                           className='filter__btn btn'
                           onClick={() => 
                                 handleSubmit(item, prod)
                           }
                        >
                           {
                           prod === categ[2] ? item : ''
                           }
                        </button>
                     </div>
                     
               )
            }):'Нет брендов'}
         </div>                      
      </div>
   )
}

export default Brand;